@import url('https://fonts.googleapis.com/css?family=Pacifico');
@import url('https://fonts.googleapis.com/css?family=Lato:400,700');


@media screen and (min-device-width: 900px) {
    .header {
        background-image: url("../media/headerBG.png");
    }

}

h1,p{
    -webkit-margin-before:0;
    -webkit-margin-after:0;
    -webkit-margin-start:0;
    -webkit-margin-end:0;
}

.header{
    background-image: url("../media/headerBG768.png");
    background-position: 50% 50%;
    width: 100%;
    height: 768px;
    background-size: cover;

}

.container{
    display: grid;
    grid-template-columns: 1fr 1fr 1.4fr;
    grid-template-rows: 120px;
    -ms-flex-align: center;
        align-items: center;
}

.logo{
    margin: 0 0 0 50px;
}

.logo__title{
    font: 32px Pacifico;
    color: #FFFA6A;
}

.logo__text{
    font: 12px Lato;
    color: #fff;
}

.phone{
    justify-self: center;
}

.phone__title{
    font: 18px Lato;
    color: #fff;
}

.phone__text{
    font: 12px Lato;
    color: #AEAEAE;

}

.nav{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    margin: auto;
}

.nav__link{
    font: 18px Lato;
    color: #fff;
    list-style-type: none;
    text-decoration: none;
    margin: 0 50px 0 0;
}

.nav__link:hover{
    color: #FFFA6A;
}

.button{
    width: 220px;
    height: 60px;
    background-color: #FFFA6A;
    border-radius: 3px;
    border: 0px solid #FCB600;
    color: #290F00;
    font: 18px Lato;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 0 0 0 10px;
    cursor: pointer;
    -webkit-box-shadow: 0 2px 6px 0 rgba(252, 182, 0, 0.2);
            box-shadow: 0 2px 6px 0 rgba(252, 182, 0, 0.2);
}

.backCall__img{
    background-image: url("../media/phone.png");
    width: 24px;
    height: 24px;
    margin: 0 5px 0 0;
}

.callMaster__img{
    background-image: url("../media/rulerSmall.png");
    width: 24px;
    height: 24px;
    margin: 0 5px 0 0;
}

.containerButton{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 180px 0 0 0;
}

.slogan{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 190px 0 0 0;
}

.slogan__title{
    font: 46px Lato;
    font-weight: 700;
    color: #fff;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 1);
}

.modal__container{
    display: grid;
    justify-items: center;
}

.modal__input{
    width: 300px;
    height: 40px;
    margin: 20px 0 0 0;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.1);
            box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.modal__input::-webkit-input-placeholder{
    padding: 0 0 0 20px;
}

.modal__input:-ms-input-placeholder{
    padding: 0 0 0 20px;
}

.modal__input::-ms-input-placeholder{
    padding: 0 0 0 20px;
}

.modal__input::placeholder{
    padding: 0 0 0 20px;
}

.modal__textarea::-webkit-input-placeholder{
    padding: 10px 0 0 20px;
}

.modal__textarea:-ms-input-placeholder{
    padding: 10px 0 0 20px;
}

.modal__textarea::-ms-input-placeholder{
    padding: 10px 0 0 20px;
}

.modal__textarea::placeholder{
    padding: 10px 0 0 20px;
}

.modal__textarea{
    width: 298px;
    margin: 20px 0 0 0;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.1);
            box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.1);

}

.modal__button{
    width: 140px;
    height: 40px;
    background-color: #FFFA6A;
    border-radius: 3px;
    border: 0px solid #FCB600;
    color: #290F00;
    font: 18px Lato;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
    justify-self: end;
    margin: 20px 0 0 0;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
}

.send__img{
    background-image: url("../media/send.png");
    width: 16px;
    height: 16px;
    margin: 0 0 0 10px;
}

.close{
    border: none;
    background-color: #fff;
    cursor: pointer;
    font-size: 20px;
    color: #A3A3A3;
    justify-self: end;
}

.modal__title{
    font: 32px Lato;
}

